import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';

@Injectable({
    providedIn: "root"
})

export class AppService {
    //baseurl = "http://localhost:8086/";
    baseurl = "http://ec2-13-233-172-180.ap-south-1.compute.amazonaws.com:5000/";

    constructor(private http: HttpClient) { }

    users(pagevalue,totalrecord): Observable<any> {
        //console.log(id);return;
        return this.http.post<any>(
            this.baseurl + "alldata",
            {pagevalue:pagevalue,totalrecord:totalrecord},
            {
                headers: new HttpHeaders({
                    "Content-Type": "application/json"
                })
            }
        );
    } 
    posttype(id,feature): Observable<any> {
        //console.log(id);return;
        return this.http.post<any>(
            this.baseurl + "changeposttype",
            {id:id,feature:feature},
            {
                headers: new HttpHeaders({
                    "Content-Type": "application/json"
                })
            }
        );
    }
    servicetype(pagevalue,totalrecord,id,service): Observable<any> {
        //console.log(id);return;
        return this.http.post<any>(
            this.baseurl + "changeservicetype",
            {pagevalue:pagevalue,totalrecord:totalrecord,id:id,service:service},
            {
                headers: new HttpHeaders({
                    "Content-Type": "application/json"
                })
            }
        );
    }
    logout(): Observable<any> { 
        return this.http.get<any>(this.baseurl + "userout");
    }
    country():Observable<any>{
        return this.http.get<any>(this.baseurl + "country");
    } 
    postdata():Observable<any>{
        return this.http.get<any>(this.baseurl + "post");
    }
    socialpostdata():Observable<any>{
        return this.http.get<any>(this.baseurl + "socialpost");
    }
    upcoming(pagevalue,totalrecord): Observable<any> {
        //console.log(id);return;
        return this.http.post<any>(
            this.baseurl + "upcoming",
            {pagevalue:pagevalue,totalrecord:totalrecord},
            {
                headers: new HttpHeaders({
                    "Content-Type": "application/json"
                })
            }
        );
    }
    category(pagevalue,totalrecord): Observable<any> {
        //console.log(id);return;
        return this.http.post<any>(
            this.baseurl + "categorys",
            {pagevalue:pagevalue,totalrecord:totalrecord},
            {
                headers: new HttpHeaders({
                    "Content-Type": "application/json"
                })
            }
        );
    }
    bookingdata():Observable<any>{
        return this.http.get<any>(this.baseurl + "allbooking");
    }
    followData():Observable<any>{
        return this.http.get<any>(this.baseurl + "follow");
    }
    paymentdata(pagevalue,totalrecord): Observable<any> {
        //console.log(id);return;
        return this.http.post<any>(
            this.baseurl + "payment",
            {pagevalue:pagevalue,totalrecord:totalrecord},
            {
                headers: new HttpHeaders({
                    "Content-Type": "application/json"
                })
            }
        );
    }
    adduser(id): Observable<any> {
        //console.log(id);return;
        return this.http.post<any>(
            this.baseurl + "register",
            { fname: id.fname,city: id.city,country : id.country,lname:id.lname,hospital:id.hospital,email:id.email,mobile:id.mobile},
            {
                headers: new HttpHeaders({
                    "Content-Type": "application/json"
                })
            }
        );
    }
    edituser(formdata,id): Observable<any> {
        //console.log(id);return;
        return this.http.post<any>(
            this.baseurl + "edituser",
            { fname: formdata.fname,city: formdata.city,country : formdata.country,lname:formdata.lname,hospital:formdata.hospital,email:formdata.email,mobile:formdata.mobile},
            {
                headers: new HttpHeaders({
                    "Content-Type": "application/json"
                })
            }
        );
    }
    resetpassword(formdata,id): Observable<any> {
        //console.log(id);return;
        return this.http.post<any>(
            this.baseurl + "setpassword",
            { password: formdata.password,cpassword: formdata.cpassword,id:id},
            {
                headers: new HttpHeaders({
                    "Content-Type": "application/json"
                })
            }
        );
    }
    forgotpassword(formdata,id): Observable<any> {
        //console.log(id);return;
        return this.http.post<any>(
            this.baseurl + "setpassword",
            { password: formdata.password,cpassword: formdata.cpassword,id:id},
            {
                headers: new HttpHeaders({
                    "Content-Type": "application/json"
                })
            }
        );
    }
    userDetail(id): Observable<any> {
        return this.http.post<any>(
            this.baseurl + "alldatabyuser",
            { id: id},
            {
                headers: new HttpHeaders({
                    "Content-Type": "application/json"
                })
            }
        );
    }
    addPostdata(id, file): Observable<any> {
        const uploaddata = new FormData();
        uploaddata.append("image", file);
        uploaddata.append("title", id.title);
        uploaddata.append("description", id.description);
        uploaddata.append("budget", id.budget);
        uploaddata.append("team", id.team);
        uploaddata.append("category", id.category);
        return this.http.post<any>(
            this.baseurl + "createpost",
            uploaddata,
        );
    }
    addEventdata(id, file,edate): Observable<any> {
        const uploaddata = new FormData();
        uploaddata.append("image", file);
        uploaddata.append("title", id.title);
        uploaddata.append("description", id.description);
        uploaddata.append("eventdate", edate);
        return this.http.post<any>(
            this.baseurl + "createevent",
            uploaddata,
        );
    }
    editeventdata(formdata,id,file,edate): Observable<any> {
        //console.log(formdata);return;
        if(file == undefined){
            file ='';
        }
        const uploaddata = new FormData();
        uploaddata.append("image", file);
        uploaddata.append("title", formdata.title);
        uploaddata.append("description", formdata.description);
        uploaddata.append("eventdate", edate);
        uploaddata.append("id", id);
        return this.http.post<any>(
            this.baseurl + "updateevent",
            uploaddata
        );
    }
    editpostdata(formdata,id,file): Observable<any> {
        //console.log(formdata);return;
        if(file == undefined){
            file ='';
        }
        const uploaddata = new FormData();
        uploaddata.append("image", file);
        uploaddata.append("title", formdata.title);
        uploaddata.append("description", formdata.description);
        uploaddata.append("budget", formdata.budget);
        uploaddata.append("team", formdata.team);
        uploaddata.append("category", formdata.category);
        uploaddata.append("id", id);
        return this.http.post<any>(
            this.baseurl + "updatepost",
            uploaddata
        );
    }
    PostDataid(id): Observable<any> {
        return this.http.post<any>(
            this.baseurl + "postid",
            { id: id},
            {
                headers: new HttpHeaders({
                    "Content-Type": "application/json"
                })
            }
        );
    }
    EventDataid(id): Observable<any> {
        return this.http.post<any>(
            this.baseurl + "eventid",
            { id: id},
            {
                headers: new HttpHeaders({
                    "Content-Type": "application/json"
                })
            }
        );
    }
    deletepost(id): Observable<any> {
        return this.http.post<any>(
            this.baseurl + "deletepost",
            { id:id},
            {
                headers: new HttpHeaders({
                    "Content-Type": "application/json"
                })
            }
        );
    }
    addcategory(id, file): Observable<any> {
        const uploaddata = new FormData();
        uploaddata.append("image", file);
        uploaddata.append("name", id.name);
        uploaddata.append("popular", id.popular);
        return this.http.post<any>(
            this.baseurl + "createcategory",
            uploaddata,
        );
    }
    editcategory(formdata,id,file): Observable<any> {
        //console.log(formdata);return;
        if(file == undefined){
            file ='';
        }
        const uploaddata = new FormData();
        uploaddata.append("image", file);
        uploaddata.append("name", formdata.name);
        uploaddata.append("popular", formdata.popular);
        uploaddata.append("id", id);
        return this.http.post<any>(
            this.baseurl + "updatecategory",
            uploaddata
        );
    }
    CategoryDetail(id): Observable<any> {
        return this.http.post<any>(
            this.baseurl + "categoryid",
            { id: id},
            {
                headers: new HttpHeaders({
                    "Content-Type": "application/json"
                })
            }
        );
    } 
    deletecategory(id): Observable<any> {
        return this.http.post<any>(
            this.baseurl + "deletecategory",
            { id:id},
            {
                headers: new HttpHeaders({
                    "Content-Type": "application/json"
                })
            }
        );
    }
    deleteupcoming(id): Observable<any> {
        return this.http.post<any>(
            this.baseurl + "deleteupcoming",
            { id:id},
            {
                headers: new HttpHeaders({
                    "Content-Type": "application/json"
                })
            }
        );
    }
    userLogin(email, password): Observable<any> {
        return this.http.post<any>(
            this.baseurl + "login",
            { email: email, password: password },
            {
                headers: new HttpHeaders({
                    "Content-Type": "application/json"
                })
            }
        );
    }
}